*{
  font-family: 'Times New Roman', Times, serif;
  text-align: center;
}

body{
  margin: 2% 15%;
}

input {
  color: white;
  width: 100%;
  font-size: 1.2rem;
}

.card-grid {
  margin-top: 50px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: #49afa8;
  padding: 50px 150px;
}

.card {
  align-content: center;
  background-color: #fff;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 12px;
  overflow: hidden;
  text-align: center;
  width: 20%;
  z-index: 0;
}

@media (max-width: 600px)  {
  .card {
    width: 35%;
    height: 220px;
  }
  .card-grid {
    z-index: 0;
  }
}
.card img {
  max-height: 170px;
  padding: 10px;
}

.card p {
  margin: 0 auto;
  text-align: center;
}


.addNew {
  font-size: 100px;
  border: 1px dashed grey ;
  z-index: 0;
  text-align: center;
}

.addNewText {
  font-size: 1rem;
}
.field {
  width: 100%;
  margin-top: 0px;
  background: #49afa8;
  padding: 15px;
  border-radius: 10px;
}

.field input {
  color: white;
}


.img-update {
  width: 200px;
  height: 200px;
  margin: 0 auto;
}

.delete {
  width: 20px;
  height: 20px;
  margin-left: 20px;
  margin-right: 50px;
}

.buttons {
  display: flex;
  align-items: center;
  margin-top: 50px;
  width: 100%;
}

.submitBtn {
  margin-left:  30px;
  width: 30%;
}